import React from 'react'
import { Issue, ServiceData } from '../Data/ServiceData'
import { Link } from 'react-router-dom'

function ServiceBanner() {
    return (
        <>
            <div className='banner text-center text-white text-uppercase'>
                <h2>Service</h2>
            </div>
            <div className="container-fluid py-5">
                <div className="container pt-5 pb-3">
                    <div className="text-center mb-3 pb-3">
                        <h1>Tours &amp; Travel Services</h1>
                    </div>
                    <div className="row">
                        {ServiceData.map((service) => (
                            <div className="col-lg-4 col-md-6 mb-4">
                                <div className="service-item bg-white text-center mb-2 py-5 px-4">
                                    <div className="margin mb-4">
                                        <i className={service.icon}></i>
                                    </div>
                                    <h5 className="mb-2 text-uppercase">{service.title}</h5>
                                    <p className="m-0">{service.description}</p>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
            <section className="other-issue-area section-gap">
                <div className="container">
                    <div className="row d-flex justify-content-center">
                        <div className="menu-content pb-70 col-lg-9">
                            <div className="title text-center">
                                <h1 className="mb-10 text-uppercase">Other issues we can help you with</h1>
                                <p>We all live in an age that belongs to the young at heart. Life that is.</p>
                            </div>
                        </div>
                    </div>
                    <div className="row mb-4">
                        {Issue.map((issue) => (
                            <div className="col-lg-3 col-md-6">
                                <div className="single-other-issue">
                                <div className="thumb" data-aos="fade-down-left" data-aos-delay="250">
                                        <img className="img-fluid" src={issue.image} alt="" />
                                    </div>
                                    <Link to="">
                                        <h4 data-aos="fade-up" data-aos-delay="250" className='text-capitalize'>{issue.title}</h4>
                                    </Link>
                                    <p data-aos="fade-down-right" data-aos-delay="250">
                                        {issue.description}
                                    </p>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </section>

        </>
    )
}

export default ServiceBanner