import React from 'react'
import { Swiper, SwiperSlide} from 'swiper/react';
import 'swiper/css';
import { TestinomialData } from '../Data/TestinomialData'
import { Autoplay,Pagination, Navigation  } from 'swiper';

function Testimonial() {
    return (
        <>
            <div data-aos="fade-up" data-aos-duration="3000" className='container-fluid text-center testimonial'>
                <Swiper
                    spaceBetween={30}
                    centeredSlides={true}
                    autoplay={{
                        delay: 2500,
                        disableOnInteraction: false,
                    }}
                    pagination={{
                        clickable: true,
                    }}
                    navigation={true}
                    modules={[Autoplay, Pagination, Navigation]}
                >
                    {TestinomialData.map((testi) => (
                        <SwiperSlide>
                            <div className="swiper-slide">
                                <div className="testimonial-item">
                                    <p>
                                        <i class="bx bxs-quote-alt-left quote-icon-left"></i>
                                        {testi.message}
                                        <i class="bx bxs-quote-alt-right quote-icon-right"></i>
                                    </p>
                                    <img src={testi.image} className="testimonial-img" alt="" />
                                    <h3>{testi.name}</h3>
                                </div>
                            </div>
                        </SwiperSlide>
                    ))}
                </Swiper>
            </div>
        </>
    )
}

export default Testimonial