import React from 'react'
import { Link } from 'react-router-dom'
import { Destination, VisitData } from '../Data/Visit'
import { motion } from 'framer-motion'


function Visit() {
    return (
        <>
            <div className="visit-country">
                <div className="container" >
                    <div className="row">
                        <div className="col-lg-5">
                            <div className="section-heading">
                                <h2>Visit One Of Our Countries Now</h2>
                                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore.</p>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div data-aos="fade-right" data-aos-offset="300"
                            data-aos-easing="ease-in-sine" className="col-lg-6">
                            <div className="items">
                                <div className="row">
                                    <div className="col-lg-12">
                                        <div className="item">
                                            <div className="row">
                                                <div className='col-lg-12'>
                                                    <ul>
                                                        {VisitData.map((item) => (
                                                            <motion.div className="card mb-3 cursor" style={{ maxWidth: 540 }} whileHover={{ scale: 1.2 }}
                                                                whileTap={{ scale: 0.9 }}
                                                                transition={{ type: "spring", stiffness: 400, damping: 17 }}>
                                                                <div className="row g-0">
                                                                    <div className="col-md-4">
                                                                        <img src={item.image} className="img-fluid rounded-start" alt='' />
                                                                    </div>
                                                                    <div className="col-md-8">
                                                                        <div className="card-body">
                                                                            <h5 className="card-title text-uppercase">{item.country}</h5>
                                                                            <span><Link to={'/'} className='sign-up primary-btn main-button button'>{item.button}</Link></span>
                                                                            <p className="card-text lh-sm">{item.description}</p>
                                                                            <p className="card-text">
                                                                                <small className="text-muted lh-sm">
                                                                                    <ul className="list-inline">
                                                                                        <li class="list-inline-item"><i className="fa fa-user" />{item.user}</li>
                                                                                        <li class="list-inline-item"><i className="fa fa-globe" /> {item.globe}</li>
                                                                                        <li class="list-inline-item"><i className="fa fa-home" />{item.home}</li>
                                                                                    </ul>
                                                                                </small>
                                                                            </p>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </motion.div>

                                                        ))}
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <ul className="page-numbers">
                                            <li><Link to="/"><i className="fa fa-arrow-left" /></Link></li>
                                            <li><Link to="/">1</Link></li>
                                            <li className="active"><Link to="/">2</Link></li>
                                            <li><Link to="/">3</Link></li>
                                            <li><Link to="/"><i className="fa fa-arrow-right" /></Link></li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div data-aos="fade-left" data-aos-offset="300"
                            data-aos-easing="ease-in-sine" className="col-lg-6">
                            <div className="container-fluid">
                                <div className="container pb-3">
                                    <div className="text-center mb-3 pb-3">
                                        <h2>Explore Top Destination</h2>
                                    </div>
                                    <div className="row">
                                        {Destination.map((dest) => (
                                            <div className="col-lg-4 col-md-6 mb-4 grid">
                                                <div className="destination-item position-relative overflow-hidden mb-2">
                                                    <img className="img-fluid" src={dest.image} alt="" />
                                                    <Link className="destination-overlay text-white text-decoration-none" to="/">
                                                        <h5 className="text-white text-center text-uppercase">{dest.country}</h5>
                                                        <span>{dest.description}</span>
                                                    </Link>
                                                </div>
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>

        </>
    )
}

export default Visit