import React from 'react'
import { Link } from 'react-router-dom'

function HomeAbout() {
    return (
        <>
            <section className="home-about-area my-5">
                <div className="container-fluid">
                    <div className="row align-items-center justify-content-end">
                        <div data-aos="fade-up" data-aos-easing="linear"
                            data-aos-duration="1500" className="col-lg-6 col-md-12 home-about-left">
                            <h1>
                                Did not find your Package? <br />
                                Feel free to ask us. <br />
                                We‘ll make it for you
                            </h1>
                            <p>
                                inappropriate behavior is often laughed off as “boys will be boys,” women face higher conduct standards especially in the workplace. That’s why it’s crucial that, as women, our behavior on the job is beyond reproach. inappropriate behavior is often laughed.
                            </p>
                            <Link to={'/'} className="primary-btn sign-up text-uppercase">request custom price</Link>
                        </div>
                        <div data-aos="fade-down" data-aos-easing="linear"
                            data-aos-duration="1500" className="col-lg-6 col-md-12 home-about-right no-padding">
                            <img className="img-fluid" src={require('../../assets/img/home-about.jpg')} alt="" />
                        </div>
                    </div>
                </div>
            </section>

        </>
    )
}

export default HomeAbout