import React from 'react'

function SignupBanner() {
    return (
        <>
            <div className='banner'>
                <div className='signup-banner text-center text-white text-uppercase'>
                    <h2>Signup</h2>
                </div>
            </div>
            <div className="container-fluid bg-registration py-5">
                <div className="container py-5">
                    <div className="row align-items-center">
                        <div className="col-lg-7 mb-5 mb-lg-0">
                            <div className="mb-4">
                                <h6 className="text-primary text-uppercase">Mega Offer</h6>
                                <h1 className="text-white"><span className="text-primary">30% OFF</span> For Group</h1>
                            </div>
                            <p className="text-white">Invidunt lorem justo sanctus clita. Erat lorem labore ea, justo dolor lorem ipsum ut sed eos,
                                ipsum et dolor kasd sit ea justo. Erat justo sed sed diam. Ea et erat ut sed diam sea ipsum est
                                dolor</p>
                            <ul className="list-inline text-white m-0">
                                <li className="py-2"><i className="fa fa-check text-primary mr-3" />Labore eos amet dolor amet diam</li>
                                <li className="py-2"><i className="fa fa-check text-primary mr-3" />Etsea et sit dolor amet ipsum</li>
                                <li className="py-2"><i className="fa fa-check text-primary mr-3" />Diam dolor diam elitripsum vero.</li>
                            </ul>
                        </div>
                        <div className="col-lg-5">
                            <div className="card border-0">
                                <div className="card-body rounded bg-white p-5">
                                    <form>
                                        <div className="form-group py-3">
                                            <input type="text" className="form-control p-4" placeholder="User name" required="required" />
                                        </div>
                                        <div className="form-group py-3">
                                            <input type="email" className="form-control p-4" placeholder="Email" required="required" />
                                        </div>
                                        <div className="form-group py-3">
                                            <input type="password" className="form-control p-4" placeholder="Password" required="required" />
                                        </div>
                                        <div className='py-3'>
                                            <button className="btn sign-up py-3" type="submit">Sign Up Now</button>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </>
    )
}

export default SignupBanner