import React from 'react'
import { Link } from 'react-router-dom'
import { About } from '../Data/AboutData'
import { motion } from "framer-motion";

function AboutBanner() {
  return (
    <>
      <div className='banner text-center text-uppercase text-white'>
        <h2>about us</h2>
      </div>
      <div className="container-fluid booking mt-5 pb-5">
        <div className="container pb-5">
          <div className="bg-light shadow padding">
            <div className="row align-items-center" style={{ minHeight: 60 }}>
              <div className="col-md-10">
                <div className="row">
                  <div className="col-md-3">
                    <div className="mb-3 mb-md-0">
                      <select className="custom-select px-4 rounded" style={{ height: 55 }}>
                        <option selected>Destination</option>
                        <option value={1}>Destination 1</option>
                        <option value={2}>Destination 1</option>
                        <option value={3}>Destination 1</option>
                      </select>
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="mb-3 mb-md-0">
                      <div className="date" id="date1" data-target-input="nearest">
                        <input type="text" className="form-control p-3 datetimepicker-input" placeholder="Depart Date" data-target="#date1" data-toggle="datetimepicker" />
                      </div>
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="mb-3 mb-md-0">
                      <div className="date" id="date2" data-target-input="nearest">
                        <input type="text" className="form-control p-3 datetimepicker-input" placeholder="Return Date" data-target="#date2" data-toggle="datetimepicker" />
                      </div>
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="mb-3 mb-md-0">
                      <select className="custom-select px-4 rounded" style={{ height: 55 }}>
                        <option selected>Duration</option>
                        <option value={1}>Duration 1</option>
                        <option value={2}>Duration 1</option>
                        <option value={3}>Duration 1</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-md-2">
                <button className="btn sign-up" type="submit" style={{ height: 55, marginTop: '-2px', padding: "0px 55px" }}>Submit</button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container-fluid py-1">
        <div className="container">
          <div className="row">
            <div className="col-lg-6" style={{ minHeight: 500 }}>
              <div className="position-relative h-100 index">
                <img className="position-absolute w-100 h-100 shadow-lg rounded border" src={require('../../assets/img/travel.jpg')} style={{ objectFit: 'cover' }} alt=""/>
              </div>
            </div>
            <div className="col-lg-6 pt-5 pb-lg-5 about">
              <div className="about-text bg-white p-4 p-lg-5 my-lg-5 shadow-lg rounded border">
                <h6 className="text-primary text-uppercase" style={{ letterSpacing: 5 }}>About Us</h6>
                <h1 className="mb-3">We Provide Best Tour Packages In Your Budget</h1>
                <p>Dolores lorem lorem ipsum sit et ipsum. Sadip sea amet diam dolore sed et. Sit rebum labore sit sit ut vero no sit. Et elitr stet dolor sed sit et sed ipsum et kasd ut. Erat duo eos et erat sed diam duo</p>
                <div className="row mb-4">
                  <motion.div className="col-6" whileHover={{ scale: 1.2 }}
                    whileTap={{ scale: 0.9 }}
                    transition={{ type: "spring", stiffness: 400, damping: 17 }}>
                    <img className="img-fluid" src={require('../../assets/img/about-1.jpg')} alt="" style={{ height: 198.13, width:317 }} />
                  </motion.div>
                  <motion.div className="col-6" whileHover={{ scale: 1.2 }}
                    whileTap={{ scale: 0.9 }}
                    transition={{ type: "spring", stiffness: 400, damping: 17 }}>
                    <img className="img-fluid" src={require('../../assets/img/about-2.jpg')} alt=""  />
                  </motion.div>
                </div>
                <Link to="" className="btn sign-up mt-1">Book Now</Link>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container-fluid py-3">
        <div className="container pb-5">
          <div data-aos="flip-up" data-aos-easing="ease-in-back"
            data-aos-delay="300"
            data-aos-offset="0" className="row cursor">
            {About.map((about) => (
              <motion.div className="col-md-4" whileHover={{ scale: 1.1 }}
                transition={{ type: "spring", stiffness: 400, damping: 10 }}>
                <div className="d-flex mb-4 mb-lg-0">
                  <div className="d-flex flex-shrink-0 align-items-center justify-content-center bg-primary rounded me-3" style={{ height: 100, width: 100 }}>
                    <i className={about.icon} />
                  </div>
                  <div className="d-flex flex-column">
                    <h5 className>{about.title}</h5>
                    <p className="m-0">{about.description}</p>
                  </div>
                </div>
              </motion.div>
            ))}
          </div>
        </div>
      </div>

    </>
  )
}

export default AboutBanner